import React from "react";
import "../sass/components/buys.scss";
import { connect } from "react-redux";
import { deleteShop } from "../redux/actions/action";

const Buys = (products) => {
  const { id, title, images, offerPricePrice, offerPrice, normalPrice } =
    products.products;
  const handleDeleteBuy = (id) => {
    products.deleteShop(id);
  };
  // function precios(offerPrice, offerPricePrice, normalPrice) {
  //   if (offerPrice === true) {
  //     console.log(normalPrice);
  //   } else {
  //     console.log(offerPricePrice);
  //   }
  // }
  return (
    <>
      <div className="container__buy">
        <img className="buy__images" src={images} alt="producto" />
        <div className="container__description">
          <h1 className="buy__title">{title}</h1>
          {/* {offerPrice ? (
            <p className="buy__price">${normalPrice}</p>
          ) : (
            <p className="buy__price">${offerPricePrice}</p>
          )} */}
          {/* {precios(offerPrice)} */}
        </div>
        <div className="buy__delete">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="1.3rem"
            height="1.3rem"
            fill="currentColor"
            className="deletebutton"
            viewBox="0 0 16 16"
            onClick={() => handleDeleteBuy(id)}
          >
            <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
            <path
              fillRule="evenodd"
              d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
            />
          </svg>
        </div>
      </div>
      {offerPrice ? console.log(normalPrice) : console.log(offerPricePrice)}
    </>
  );
};
const mapDispatchToProps = {
  deleteShop,
};
export default connect(null, mapDispatchToProps)(Buys);
